.game-renderer {
  position: relative;
  width: 640px;
  margin: 0 auto;
}
.game-renderer__canvas {
  position: absolute;
  z-index: 1;
  top: 0;
  left: 0;
}
.game-renderer__canvas_ui {
  z-index: 2;
}
.game-renderer__canvas_3d {
  background: #303030;
}


